import React, { Component } from "react";
import { connect } from "react-redux";
import { compose } from "redux";

import CreateForm from "./createForm";

import notification from "../../../../components/notification";
// import { Input, Select, Divider } from "antd";

import LayoutWrapper from "../../../../components/utility/layoutWrapper";
import PageHeader from "../../../../components/utility/pageHeader";
import IntlMessages from "../../../../components/utility/intlMessages";
import Button from "../../../../components/uielements/button";
import actions from "../../../../redux/supplier/courier/actions";
import { Box } from "../../../App/app.style";
// import { Tabs } from "antd";


// const TabPane = Tabs.TabPane;
const redirectTo = "/dashboard/setup/couriers";

class CreateCourier extends Component {

  // Get All data diawal
  componentDidMount() {
    const { createCourier } = this.props;
    createCourier();
  }

  handleSubmit = value => {
    const { storeCourier } = this.props;
    storeCourier(value);
  };

  componentDidUpdate() {
    // Display error notif
    if (this.props.courier.error) {
      notification("error", this.props.courier.error);
      this.props.clearError();
    }

    if (this.props.courier.success) {
      //this.props.history.replace(redirectTo+"/update/"+this.props.courier.id_courier);
      this.props.history.replace(redirectTo)
    }
  }



  render() {
    // console.log(this.props);
    return (
      <LayoutWrapper>
        <Box>
          <PageHeader
            title={<IntlMessages id="couriers.add.title" />}
            marginBottom={true}
          >
            <Button
              onClick={() => this.props.history.replace(redirectTo)}
              style={{ marginLeft: "5px" }}
            >
              <IntlMessages id="button.back" />
            </Button>
          </PageHeader>

          <CreateForm
            auth={this.props.auth}
            submit={this.handleSubmit}
            courier={this.props.courier}
          />
        </Box>
      </LayoutWrapper>
    );
  }
}

const mapStateToProps = state => {
  return {
    auth: state.auth,
    courier: state.courier
  };
};

export default compose(
  connect(
    mapStateToProps,
    actions
  )
)(CreateCourier);
